<template>
  <v-container fluid fill-height style="padding:0;" id="formdata" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="FormData">FormData</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>FormData</code>对象的设计初衷是将form元素的input表单数据收集起来，更加方便的处理表单数据</p>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    1. 创建空的formData对象
        let formData = new FormData();
    
    2. 根据form元素创建，会自动将form标签内的所有input标签数据添加到对象中，input标签需要指定name属性
        let formData = new FormData(formElement);

    3. 添加键值对，每一个name可以对应多个值
        其中value是字符串
            formData.append(name, value);
        其中value是blob对象或者file对象，需要指定第三个参数表示文件名
            formData.append(name, blob || file, filename);

    4. 删除键值对
        formData.delete(name);
    
    5. 获取可遍历对象
        formData.entries();
        formData.keys();
        formData.values();

    6. 根据键名获取第一个键值
        formData.get(name);

    7. 根据键名获取所有键值
        formData.getAll(name);
    
    8. 判断是否存在指定键名
        formData.has(name);

    9. 修改键值对
        其中value是字符串
            formData.set(name, value);
        其中value是blob对象或者file对象，需要指定第三个参数表示文件名   
            formData.set(name, blob || file, filename);
    `
],
      time: "2019-07-16",
      title: "FormData",
      items: [
        {
          title: "FormData",
          target: "#FormData"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#formdata {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

